.footer-links {
  border-top: 1px solid gray;
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  gap: 1.75rem;
  margin-top: 2rem;
  margin-left: 1rem;
}

.footer-links a {
  text-decoration: none;
  color: inherit;
  font-weight: 700;
}

.social-links {
  display: flex;
  gap: 1rem;
  margin: 1rem 0;
}

.language {
  width: fit-content;
  background-color: white !important;
  color: #008248 !important;
  border: 1px solid #05bf6b;
  font-weight: 700 !important;
  padding: 7px 14px;
  margin-bottom: 4rem;
}
