body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Gotham Screensmart Book", sans-serif;
  background-color: #f7f7f7;
}

@font-face {
  font-family: "Gotham Screensmart Book";
  src: url("../public/Gotham-Font/GothamBook.ttf") format("woff"),
    url("../public/Gotham-Font/Gotham-Bold.otf") format("woff"),
    url("../public/Gotham-Font/GothamMedium.ttf") format("woff"),
    url("../public/Gotham-Font/Gotham-Light.otf") format("woff");
}

.green-link-button {
  background-color: #008248;
  color: white !important;
  padding: 9px 15px;
  border-radius: 50px;
  transition: background-color 0.25s ease-out;
}
.green-link-button:hover {
  background-color: #05bf6b;
}
