.rewards-container {
  margin-top: 5.5rem;
}

.rewards-page-left {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40vw;
  background-color: rgb(15, 45, 39);
  height: 100%;
  margin-top: -5.5rem;
}

.starbucks-rewards-image {
  width: 260px;
  background-color: rgb(15, 45, 39);
}

.rewards-page-right {
  margin-left: 40vw;
  display: flex;
  flex-direction: column;
}

.rewards-header {
  padding: 0 2rem 0 1.25rem;
  margin: 1.5rem 0 0 0;
  font-size: 1.6rem;
  font-weight: 700;
}

.rewards-text {
  padding: 0 2rem 0 1.25rem;
}

.touch-coffee {
  margin-top: 1.25rem;
  background-color: hsla(0, 0%, 75.7%, 0.14902);
  padding: 2rem;
  display: flex;
  flex-direction: column;
}

.touch-coffee-image {
  max-width: 11rem;
  margin-right: 3rem;
}

.touch-coffee-header {
  font-size: 1.25rem;
  font-weight: 700;
}

.touch-coffee-top-text {
  font-weight: 700;
  margin: 0 0 1.25rem 0;
}

.touch-coffee-bottom {
  display: flex;
  flex-direction: row;
}

.touch-coffee-text {
  line-height: 1.25rem;
}

.secondary-button {
  background-color: inherit;
  color: #008248 !important;
  border: 1px solid #05bf6b;
  text-decoration: none;
  font-weight: 700;
  font-size: 0.9rem;
}
.secondary-button:hover {
  background-color: rgb(231, 231, 231) !important;
}

.rewards-small-header {
  padding: 0 2rem 0 1.25rem;
  font-size: 1.15rem;
  font-weight: 700;
  margin: 0.5rem 0 0 0;
}
