.home-container {
  display: flex;
  margin-top: 5.5rem;
}

.home-page-left {
  position: fixed;
}

.home-page-video {
  margin-top: -5rem;
  min-height: 100vh;
  max-width: 40vw;
  background-color: rgb(214, 239, 223);
}

.home-page-left-text {
  position: absolute;
  bottom: 4.5rem;
  left: 2.5rem;
  z-index: 9;
  font-size: 2rem;
}

.home-page-right {
  margin-left: 40vw;
  display: flex;
  flex-direction: column;
  padding: 1.5rem 2rem 1rem 2.5rem;
}

.iced-shaken-espresso {
  width: 100%;
}

.yulafli {
  padding: 2.5rem 4rem 2rem 2.25rem;
}

.yulafli-header {
  font-weight: 700;
  font-size: 1.2rem;
  margin: 0;
}

.yulafli-text {
  font-size: 0.9rem;
  line-height: 1.3rem;
  margin: 1.25rem 0;
}

.yulafli a {
  text-decoration: none;
  color: rgb(86, 79, 79);
  font-size: 0.8rem;
  font-weight: 700;
  cursor: pointer;
}

.getiryemek {
  background-color: white !important;
  color: #008248 !important;
  border: 1px solid #05bf6b;
  margin-left: 1rem;
}
.getiryemek:hover {
  background-color: rgb(231, 231, 231) !important;
}

.home-right-box-container {
  display: flex;
  flex-direction: column;
  gap: 3.5rem;
}
